.loader {
  width: 40px;
  height: 60px;
  color: rgba(0, 0, 0, 0);
  animation: heartBeat 1.2s cubic-bezier(.215, .61, .355, 1) infinite;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  width: 40px;
  height: 60px;
  transform-origin: 50% 68%;
  background: #ff3d00;
  border-radius: 50px 50px 0 0;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: rotate(45deg);
  box-shadow: inset 5px 4px 5px rgba(0, 0, 0, .267);
}

.loader:after {
  transform: rotate(-45deg);
}

@keyframes heartBeat {
  0% {
    transform: scale(.95);
  }

  5% {
    transform: scale(1.1);
  }

  39% {
    transform: scale(.85);
  }

  45% {
    transform: scale(1);
  }

  60% {
    transform: scale(.95);
  }

  100% {
    transform: scale(.9);
  }
}

